  private _recs: Map<number, any>
  private _search_result_id: number
  @ $mol_mem_key
  rec_fld(idx_fld: any, val?: any, force?: $mol_atom_force) {
    if (val !== void 0) {
      if (!this._recs) this._recs = new Map<number, any>()
      let rec = this._recs.get(idx_fld.idx)
      if (!rec) {
        rec = new Map<string, any>()
        this._recs.set(idx_fld.idx, rec)
      }
      rec.set(idx_fld.fld, val)
      return val
    }
    if (this.search_result_id() !== this._search_result_id && this._recs) {
      this._recs = void 0
      this._search_result_id = this.search_result_id()
    }
    let result: string

    if (!this._recs) this._recs = new Map<number, any>()
    const rec = this._recs.get(idx_fld.idx)
    if (rec && rec.has(idx_fld.fld)) {
      result = rec.get(idx_fld.fld)
    } else {
      if (!rec || !rec.has('guid')) {
        if (!this._rec_fld_request_by_idx) this._rec_fld_request_by_idx = new Map<number, Set<string>>()
        let request = this._rec_fld_request_by_idx.get(idx_fld.idx)
        if (!request) {
          request = new Set<string>(['guid', 'w6_offer_id', 'object_guid'])
          this._rec_fld_request_by_idx.set(idx_fld.idx, request)
        }
        request.add(idx_fld.fld)
      } else {
        if (!this._rec_fld_request_by_guid) {
          this._rec_fld_request_by_guid = new Map<string, Set<string>>()
        }
        const guid = rec.get('guid')
        let request = this._rec_fld_request_by_guid.get(guid)
        if (!request) {
          request = new Set<string>()
          this._rec_fld_request_by_guid.set(guid, request)
          if (!this._idx_by_guid) this._idx_by_guid = new Map<string, number>()
          this._idx_by_guid.set(guid, idx_fld.idx)
        }
        request.add(idx_fld.fld)
      }
      if (!this._rec_fld_waiting) {
        const doRequest = () => {
          if (!this._rec_fld_request_by_guid) this._idx_by_guid = void 0
          if (!this._rec_fld_request_by_guid && !this._rec_fld_request_by_idx) return
          const start = Date.now()
          this._rec_fld_waiting = true
          setTimeout(() => {
            if (this._rec_fld_request_by_guid) {
              this.request_data_worker({cmd: 'recs', by_guid: this._rec_fld_request_by_guid}, (event: any) => {
                this._rec_fld_waiting = false
                if (event.data.status == 'ok') {
                  console.log({'': 'by_guid', brutto: Date.now() - start, netto: event.data.timing, count: event.data.by_guid.size})
                  event.data.by_guid.forEach((fld_values: any, guid: string) => {
                    const idx = this._idx_by_guid.get(guid)
                    // console.log({idx, guid})
                    if (idx !== void 0) {
                      fld_values.forEach((fld_value: any, fld: string) => {
                        this.rec_fld({idx, fld}, fld_value)
                        // console.log({idx, fld, fld_value})
                      })
                    }
                  })
                }
                doRequest()
              })
              this._rec_fld_request_by_guid = void 0
            } else if (this._rec_fld_request_by_idx) {
              this.request_data_worker({cmd: 'recs', by_idx: this._rec_fld_request_by_idx}, (event: any) => {
                this._rec_fld_waiting = false
                if (event.data.status == 'ok') {
                  console.log({'': 'by_idx', brutto: Date.now() - start, netto: event.data.timing, count: event.data.by_idx.size})
                  event.data.by_idx.forEach((fld_values: any, idx: number) => {
                    fld_values.forEach((fld_value: any, fld: string) => {
                      this.rec_fld({idx, fld}, fld_value)
                      // console.log({idx, fld, fld_value})
                    })
                  })
                }
                doRequest()
              })
              this._rec_fld_request_by_idx = void 0
            } else {
              this._rec_fld_waiting = false
            }
          }, 16)
        }
        doRequest()
      }
      result = void 0
    }
    return result
  }
  private _rec_fld_request_by_idx: Map<number, Set<string>>
  private _rec_fld_request_by_guid: Map<string, Set<string>>
  private _idx_by_guid: Map<string, number>
  private _rec_fld_waiting: boolean
  @ $mol_mem_key
  cell_content(ij: any) {
    let result = ''
    // return result
    const idx = this.row_def_idx(ij.i)
    if (ij.id == 'blank') {
      result = '' //+ idx
    } else {
      if (idx !== void 0) {
        const col_def = this.col_defs_default()[ij.id]
        const fld = col_def.fld || ij.id
        const fld_list = Array.isArray(fld) ? fld : [fld]
        const value_list = [] as any[]
        const count = fld_list.length
        let has_undef = false
        for (let i = 0; i < count; i++) {
          const value = this.rec_fld({idx, fld: fld_list[i]})
          if (value === void 0) has_undef = true
          value_list.push(value)
        }
        if (!has_undef)
          result = typeof col_def.formatter != 'function' ?
            `${value_list}` :
            col_def.formatter.apply(this, value_list)
      }
    }
    return result
  }
  @ $mol_mem_key
  row_note(i: number) {
    const idx = this.row_def_idx(i)
    const result = this.rec_fld({idx, fld: 'note'}) as string
    if (result) {
      new $mol_defer(() => {
        const dom_node = this.Note(i).dom_node() as HTMLElement
        const height = dom_node.scrollHeight// + 6 * 2 + 2;
        const delta = height - dom_node.offsetHeight
        // console.log(delta, i, dom_node.innerText, dom_node.clientHeight, dom_node.scrollHeight)
        if (delta > 0) {
          let {note, footer} = this.row_footer_height_of_idx(idx)
          note += delta
          footer += delta
          // console.log({note, footer})
          this.row_footer_height_of_idx(idx, {note, footer})
          // this.row_footer_height_of_idx(idx, this.row_footer_height(i) + delta)
          // this.row_note_height(i, height)
        }
      })
    }
    return result
  }
