  @ $mol_mem
  col_ids_visible(val?: string[], force?: $mol_atom_force ) {
    const col_defs = this.col_defs()
    if (val !== void 0) {
      const col_defs_new = new Map<string, BwGrid2ColDef>()
      const count = val.length
      const cols_visible = new Set<string>()
      for (let i = 0; i < count; i++) {
        const col_id = val[i]
        if (!col_defs.has(col_id)) continue
        const col_def = col_defs.get(col_id)
        if (col_def.hidden) col_def.hidden = false
        col_defs_new.set(col_id, col_def)
        cols_visible.add(col_id)
      }
      [...col_defs.keys()].filter(col_id => !cols_visible.has(col_id)).forEach(col_id => {
        const col_def = col_defs.get(col_id)
        if (!col_def.hidden) col_def.hidden = true
        col_defs_new.set(col_id, col_def)
      })
      this.col_defs(col_defs_new)
    }
    const result =
      [...this.col_defs().entries()]
      .filter((entry: [string, BwGrid2ColDef]) => !entry[1].hidden)
      .map((entry: [string, BwGrid2ColDef]) => entry[0]) //as any as string[]
    // const col_ids_visible_set_new = new Set<string>(result)
    // if (!$bw_equal(col_ids_visible_set_new, this.col_ids_visible_set())) {
    //   this.col_ids_visible_set(col_ids_visible_set_new)
    // }
    return result
  }
