      if (Number.isFinite(price)) price = Math.floor(Math.abs(price))
      if (!price) return '?'
      let result = price.toString();
      const pattern = /(-?\d+)(\d{3})/;
      while (pattern.test(result)) result = result.replace(pattern, "$1 $2")
      return result
